* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#container {
  background-color: #353535;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.start {
  color: #fff;
  cursor: pointer;
  z-index: 0;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background: #111;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: .6em 2em;
  position: absolute;
  top: 20px;
  left: 15px;
}

.start:before {
  content: "";
  z-index: -1;
  -webkit-filter: blur(5px);
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 10px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  transition: opacity .3s ease-in-out;
  animation: 20s linear infinite glowing-button-85;
  position: absolute;
  top: -2px;
  left: -2px;
}

@keyframes glowing-start {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.start:after {
  z-index: -1;
  content: "";
  background: #222;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.load {
  color: #fff;
  font-size: 24px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
